var tlMap = new TimelineLite();

$(document).ready(function () {

  //documentMinimumHeight();
  //$('body').css('height',3000);

  $( "#menu-toggle" ).on('click', bugerMenuClickEvent);
  $(window).on('scroll', onScroll);

  pageInit();
  initBarbaJs();

  var mySwiper = new Swiper ('.swiper-container', {
    // Optional parameters
    loop: true,
    effect:'fade',
    // Navigation arrows
    navigation: {
      nextEl: '.swiper-button-next',
      prevEl: '.swiper-button-prev',
    },
  });


});



/*========================================================*/
/*======================= Page Init ======================*/
/*========================================================*/
function pageInit() {

  tlMap
    //.to('#africa',3,{ attr:{scale:4, viewBox:"50 140 57 472"}, ease: Power2.easeIn } )
    .set('.map-africa',{ transformOrigin: '75px 197px' } )
    .to('.map-africa',5,{ scale:14, ease: Power2.easeInOut } )
    .to('#africa .st0',3,{  fill:'#ffffff', stroke:'#fff', ease: Power2.easeIn },'-=5' )
    .to('#senegal',3,{fill:'#ece5e2', stroke:'#b3aba7', ease: Power2.easeIn },'-=3')
    .to('#gambie',3,{stroke:'#b3aba7', ease: Power2.easeIn },'-=2')

    .to('.map-africa',1,{opacity:0,display:'none', ease: Power2.easeOut},'-=4')

    .to('.map-senegal',4,{opacity:1 , ease: Power2.easeOut},'-=4.1')
    .fromTo('#senegalMap', 4, { scale:0.3 }, { scale: 1, ease: Power2.easeOut },'-=4.1')
    
    .set('#senegalMap',  { transformOrigin: 'left'} )
    .set('.map-porte',  { scale: 0.5, transformOrigin: '47px 220px', zIndex:10} )
    .addLabel('fadeImg', '+=2')
    .to('#senegalMap', 3, { scale: 5, ease: Power2.easeIn },'fadeImg')
    .to('.map-senegal',2,{opacity:0 ,display:'none', ease: Power2.easeIn}, '-=2')
    .to('.map-porte',3,{opacity:1, scale:1, ease: Power2.easeOut}, '-=1')
    
  ;

  //tlMap.pause();

}

/*========================================================*/
/*======================= Scroll ====================*/
/*========================================================*/
function onScroll(){

  var scrollTop = $(window).scrollTop();
  if (scrollTop >= 100) {
    $('.header-wrapper').addClass('scrolled');
  } else {
    $('.header-wrapper').removeClass('scrolled');
  } 
}


/*========================================================*/
/*======================= Burger menu ====================*/
/*========================================================*/
function bugerMenuClickEvent(event) {
  if ( !$('.icon-burger').hasClass('open') ){
    $('.icon-burger').addClass('open');
    $('.navigation-mobile-wrapper').addClass('open');
  } else {
    closeMenu();
  }
}

function closeMenu(){
  if ( $('.icon-burger').hasClass('open') ){
    $('.navigation-mobile-wrapper').removeClass('open');
    $('.icon-burger').removeClass('open');
  }
}


/*========================================================*/
/*======================= Min height ====================*/
/*========================================================*/
function documentMinimumHeight() {
  var wH = $(window).height();
  if( wH > $('body').height() )
    $('body').css('height',wH);
}

